import uniIcons from "@/components/uni-ui/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			 username:"",
			 password:""
		}
	},
	onLoad() {
	},
	components: {uniIcons},
	methods: {
		removeInput(value){
			if(value == 'password'){
				this.password = ''
			}else{
				this.username = ''
			}
		},
		login(){
			uni.switchTab({
				url:"/pages/tabBar/userCenter"
			})
		}
	}
}
